<template>
  <div id="info">
    <h6>{{mock.title}}合作商家</h6>
    <p class="title_p">点击下方图片可看大图</p>
    <section>
      <ul>
        <li
          v-for="(item,index) in mock.data"
          :key="index"
        >
          <img
            v-lazy="image"
            preview="10"
            :preview-text="item.text"
            alt=""
            v-for="(image,index) in item.images"
            :key="index"
          >
          <p>{{item.text}}</p>
        </li>
      </ul>
    </section>
  </div>
</template>

<script>
import Mock from "../mock/mock.js";
export default {
  name: "info",
  data() {
    return {
      msg: "Welcome to info",
      mock: {}
    };
  },
  created() {},
  mounted() {
    let type = this.getQueryParam("type");
    this.mock = Mock[type];
    this.$previewRefresh();
  },
  methods: {
    getQueryParam(name) {
      var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)");
      //var r = window.location.search.substr(1).match(reg);
      var r = window.location.hash.split("?")[1].match(reg);
      if (r != null) return decodeURI(r[2]);
      return false;
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#info {
  background: #fff;
}
h6 {
  font-size: 0.3rem;
  text-align: center;
  height: 0.4rem;
  line-height: 0.4rem;
  background: -webkit-linear-gradient(
    left,
    #2318f2 0%,
    skyblue 50%,
    #2318f2 100%
  );
  color: #fff;
  padding: 0.1rem 0;
}
.title_p {
  font-size: 0.18rem;
  text-align: center;
  margin-top: 0.1rem;
}
section li {
  margin-top: 0.2rem;
}
section p {
  font-size: 0.18rem;
  text-align: center;
  font-weight: normal;
}
section img {
  width: 3rem;
  height: 3rem;
  margin: 0rem auto 0.1rem;
  display: block;
}
</style>



